A1wC8B;EACK,0BAAA;EACA,mBAAA;AA4wCnC;AAxwCyB;EACK,WAAA;EACA,YAAA;EACA,kBAAA;EACA,SAAA;EACA,qBAAA;AA0wC9B;AAxwC8B;EACK,yBCzC1B;ADmzCT;AAvwC8B;EACK,yBC5CrB;ADqzCd;AAtwC8B;EACK,yBC/CpB;ADuzCf;AArwC8B;EACK,yBCxDpB;AD+zCf;AApwC8B;EACK,sBC1DxB;ADg0CX;AAnwC8B;EACK,yBC/DrB;ADo0Cd;AAjwCmC;EACK,aAAA;AAmwCxC;AA/vC8B;EACK,gBAAA;EACA,kBAAA;AAiwCnC","sourcesContent":["@import 'scss/global.scss';\n\n\n.editor-styles-wrapper {\n     padding-left: 2rem !important;\n     padding-right: 2rem !important;\n\n     .block-editor-block-list__block {\n          margin: 0 auto;\n          padding: 1rem 0;\n     }\n\n     .acf-field[data-name=\"colour_picker\"] {\n          ul.acf-radio-list {\n               display: flex;\n               flex-wrap: wrap;\n               gap: 1rem;\n               align-items: flex-start;\n               overflow: visible;\n\n               &:focus-within {\n                    border-color: transparent;\n               }\n\n               li {\n                    width: fit-content;\n                    margin: 0 8px 8px 0;\n                    border-radius: 4px;\n                    display: flex;\n\n                    &:nth-child(3n) {\n                         margin-right: 0;\n                    }\n\n                    label {\n                         display: flex;\n                         flex-direction: column;\n                         width: 100%;\n                         justify-content: center;\n                         align-items: center;\n                         text-align: center;\n                         line-height: 1.2;\n                         font-size: 10px;\n                         text-transform: uppercase;\n                         font-weight: 700;\n                         border-radius: 4px;\n\n                         &.selected {\n                              input {\n                                   outline: 2px solid #2271b1;\n                                   outline-offset: 3px;\n                              }\n                         }\n\n                         input[type=\"radio\"] {\n                              width: 3rem;\n                              height: 36px;\n                              border-radius: 4px;\n                              margin: 0;\n                              margin-bottom: 0.5rem;\n\n                              &[value=\"red\"] {\n                                   background-color: $pf-red;\n                              }\n\n                              &[value=\"dark-red\"] {\n                                   background-color: $pf-dark-red;\n                              }\n\n                              &[value=\"light-red\"] {\n                                   background-color: $pf-light-red;\n                              }\n\n                              &[value=\"off-white\"] {\n                                   background-color: $pf-off-white;\n                              }\n\n                              &[value=\"white\"] {\n                                   background-color: $pf-white;\n                              }\n\n                              &[value=\"charcoal\"] {\n                                   background-color: $pf-charcoal;\n                              }\n\n                              &:checked {\n                                   &::before {\n                                        display: none;\n                                   }\n                              }\n\n                              &:focus {\n                                   box-shadow: none;\n                                   border-color: none;\n                              }\n                         }\n                    }\n               }\n          }\n     }\n}","$pf-base-size: 1rem;\n\n/**********************\n Container sizes\n***********************/\n$col--sm: 540px; // (max-width: 540px)\n$col--md: 720px; // (max-width: 720px)\n$col--lg: 960px; // (max-width: 960px)\n$col--xl: 1140px; // (max-width: 1140px)\n\n/**********************\n Colours\n***********************/\n\n// Brand colours\n/**\n  These colours are used for the primary brand colours. They must be entered in to the Restricted colour picker options in the WordPress admin.\n*/\n$pf-off-white: #f8faf8;\n$pf-charcoal: #262626;\n$pf-white: #fff;\n$pf-red: #a43939;\n$pf-dark-red: #802727;\n$pf-light-red: #bd4c4c;\n\n\n// Text colours\n$body-text-color: $pf-charcoal;\n\n/**********************\n Breakpoints\n***********************/\n\n$breakpoints: (\n  sm: (\n    breakpoint: 576px,\n    max-width: 540px,\n  ),\n  md: (\n    breakpoint: 768px,\n    max-width: 720px,\n  ),\n  lg: (\n    breakpoint: 992px,\n    max-width: 960px,\n  ),\n  xl: (\n    breakpoint: 1200px,\n    max-width: 1140px,\n  ),\n  xxl: (\n    breakpoint: 1650px,\n  ),\n);\n\n$size--xs: $pf-base-size * 0.6875;\n//11px\n$size--sm: $pf-base-size * 0.8125;\n//13px\n$size--base: $pf-base-size;\n//16px\n$size--md: $pf-base-size * 1.125;\n//18px\n$size--lg: $pf-base-size * 1.5;\n//24px\n$size--xl: $pf-base-size * 1.875;\n// 30px\n$size--xxl: $pf-base-size * 3.125;\n//50px\n\n/**********************\n Spacing\n***********************/\n\n$spacing--xs: $pf-base-size * 1;\n$spacing--sm: $pf-base-size * 1.5;\n$spacing--md: $pf-base-size * 2;\n$spacing--lg: $pf-base-size * 3;\n$spacing--xl: $pf-base-size * 4;\n\n/**********************\nFonts\n***********************/\n\n$font--primary: 'Josefin Sans', Helvetica, Arial, sans-serif;\n$font--secondary: 'Josefin Sans', Helvetica, Arial, sans-serif;\n$font--tertiary: 'Montserrat', Helvetica, Arial, sans-serif;\n","// Modern CSS reset: https://github.com/hankchizljaw/modern-css-reset\n\n/* Box sizing rules */\n*,\n*::before,\n*::after {\n    box-sizing: border-box;\n}\n\n// /* Remove default padding */\n// ul[class],\n// ol[class] {\n//     padding: 0;\n// }\n\n/* Remove default margin */\nbody,\nh1,\nh2,\nh3,\nh4,\np,\nul,\nol,\nfigure,\nblockquote,\ndl,\ndd {\n    margin: 0;\n}\n\n/* Set core root defaults */\nhtml {\n    scroll-behavior: smooth;\n}\n\n/* Set core body defaults */\nbody {\n    min-height: 100vh;\n    text-rendering: optimizeSpeed;\n    line-height: 1.5;\n}\n\n/* Remove list styles on ul, ol elements with a class attribute */\nul[class],\nol[class] {\n    list-style: none;\n}\n\n/* A elements that don't have a class get default styles */\na:not([class]) {\n    text-decoration-skip-ink: auto;\n}\n\n/* Make images easier to work with */\nimg,\npicture {\n    max-width: 100%;\n    display: block;\n}\n\n/* Natural flow and rhythm in articles by default */\narticle>*+* {\n    margin-top: 1em;\n}\n\n/* Inherit fonts for inputs and buttons */\ninput,\nbutton,\ntextarea,\nselect {\n    font: inherit;\n}\n\n/* Blur images when they have no alt attribute */\nimg:not([alt]) {\n    filter: blur(10px);\n}\n\n/* Remove all animations and transitions for people that prefer not to see them */\n@media (prefers-reduced-motion: reduce) {\n    * {\n        animation-duration: 0.01ms !important;\n        animation-iteration-count: 1 !important;\n        transition-duration: 0.01ms !important;\n        scroll-behavior: auto !important;\n    }\n}","/**\nThe function `map-deep-get` is a custom function written in a Sass/SCSS stylesheet. Its purpose is to retrieve a nested value from a map (also known as a dictionary or associative array) using a variable number of keys.\n\nHere's a breakdown of how the function works:\n\n1. The function is defined using the `@function` directive and named `map-deep-get`. It takes two parameters:\n   - `$map`: The map from which the value will be retrieved.\n   - `$keys...`: A variable number of keys that specify the path to the desired value in the map.\n\n2. Inside the function, there is a `@each` loop that iterates over each `$key` provided in `$keys...`.\n\n3. Within the loop, the `map-get()` function is used to retrieve the value associated with the current `$key` from the `$map`. The retrieved value is then assigned back to the `$map` variable, effectively updating it to the nested map.\n\n4. After the loop finishes, the final value of `$map` is returned using the `@return` directive.\n\nIn summary, the `map-deep-get` function allows you to access a nested value within a map by providing a sequence of keys that define the path to the desired value. Each key is used to access a nested map until the final value is reached, which is then returned.\n*/\n\n@function map-deep-get($map, $keys...) {\n\t@each $key in $keys {\n\t\t$map: map-get($map, $key);\n\t}\n\t@return $map;\n}",".container {\n  position: relative;\n  margin: 0 auto;\n  width: 100%;\n  padding: 0 $size--md;\n  box-sizing: border-box;\n\n  @include media-breakpoint('sm') {\n    max-width: $col--sm;\n  }\n\n  @include media-breakpoint('md') {\n    max-width: $col--md;\n  }\n\n  @include media-breakpoint('lg') {\n    max-width: $col--lg;\n  }\n\n  @include media-breakpoint('xl') {\n    max-width: $col--xl;\n  }\n}\n","@mixin media-breakpoint($breakpoint, $property: min-width) {\n  @media ($property: map-deep-get($breakpoints, $breakpoint, breakpoint)) { @content; }\n}\n\n",".full-bleed {\n    margin-left: calc((100vw - 100%) / -2);\n    width: 100vw;\n}\n",".flow > * + * {\n    margin-top: var(--flow-space, 1em);\n}",".visually-hidden {\n    border: 0;\n    clip: rect(0 0 0 0);\n    height: auto;\n    margin: 0;\n    left: -10000px;\n    opacity: 0;\n    transform: scale(0);\n    overflow: hidden;\n    padding: 0;\n    position: absolute;\n    width: 1px;\n    white-space: nowrap;\n  }",".block-padding {\n    padding-top: 3rem;\n    padding-bottom: 3rem;\n}","/**\n * Background Colours\n * -----------------------------------------------------------------------------\n */\n\n.bg--red {\n    background-color: $pf-red;\n    color: $pf-white;\n}\n\n.bg--dark-red {\n    background-color: $pf-dark-red;\n    color: $pf-white;\n}\n\n.bg--light-red {\n    background-color: $pf-light-red;\n    color: $pf-white;\n}\n\n.bg--off-white {\n    background-color: $pf-off-white;\n    color: $pf-charcoal;\n}\n\n.bg--white {\n    background-color: $pf-white;\n    color: $pf-charcoal;\n}\n\n.bg--charcoal {\n    background-color: $pf-charcoal;\n    color: $pf-white;\n}\n\n/**\n * Text Colours\n * -----------------------------------------------------------------------------\n */\n.red {\n    color: $pf-red;\n}\n\n.dark-red {\n    color: $pf-dark-red;\n}\n\n.light-red {\n    color: $pf-light-red;\n}\n\n.off-white {\n    color: $pf-off-white;\n}\n\n.white {\n    color: $pf-white;\n}\n\n.charcoal {\n    color: $pf-charcoal;\n}\n\n",".overlay {\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    background-color: rgba(0, 0, 0, 0.3);\n}","@import url('https://fonts.googleapis.com/css2?family=Barlow&display=swap');\n@import url('https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@600;700&display=swap');\n@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500&display=swap');\n\n// Primary font - Josefin Sans (300, 400, 700)\n@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300;400;700&display=swap');\n\nbody, \np, \ndiv {\n  font-family: $font--primary;\n  font-size: $size--md;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  line-height: 1.2;\n  font-family: $font--secondary;\n  text-transform: uppercase;\n}\n\n.h1, h1:not([class]) {\n  font-size: $size--xxl;\n}\n\n.h2, h2:not([class]),\n.h3, h3:not([class]) {\n  font-size: $size--xl;\n}\n\na {\n  color: $pf-dark-red;\n  text-decoration: underline;\n}\n\nul {\n  li {\n    list-style-type: none;\n  }\n  li:before {\n    content:\"Â·\";\n    font-size:120px;\n    vertical-align:middle;\n    line-height:20px;\n  }\n}\n\n.button {\n  font-size: rem(18);\n\n  @include media-breakpoint('lg') {\n    font-size: rem(22);\n  }\n}\n\n.nav {\n    font-size: $size--md;\n}\n\n@include media-breakpoint('lg') {\n  .nav {\n    font-size: $size--md;\n\n    .sub-menu {\n      font-size: $size--md;\n    }\n  }\n}\n","// Header specific variables\n$header-height-mobile: 78px;\n$header-height-desktop: 106px;\n\n// TODO: add box shadow to bottom of header when nav is open on mobile as its hard to see if the section below it is the same colour\n\n.header {\n  color: $pf-charcoal;\n  background-color: $pf-white;\n  position: sticky;\n  top: 0;\n  height: $header-height-mobile;\n  font-size: $size--base;\n  z-index: 10000;\n  box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.50);\n\n\n  @include media-breakpoint('lg') {\n    height: $header-height-desktop;\n  }\n\n  &__wrapper {\n    height: 100%;\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n\n    &__nav {\n      height: 100%;\n      display: flex;\n      align-items: center;\n    }\n  }\n\n  .nav {\n    /*\n  * MOBILE STYLING\n  */\n    @media screen and (max-width: 992px) {\n      background-color: $pf-dark-red;\n      color: $pf-off-white;\n      position: absolute;\n      left: 0;\n      top: $header-height-mobile;\n      width: 100%;\n      max-height: 0;\n      transition: max-height 1s;\n      overflow: hidden;\n      @include full-bleed;\n\n      &.drop-open {\n        height: fit-content;\n        max-height: 2000px;\n        transition: max-height 1s;\n        box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.50);\n      }\n\n      .main-navigation {\n        margin-top: 2rem;\n      }\n\n      .menu-item {\n        & .sub-menu-btn {\n          display: block;\n\n          &:hover,\n          &:focus {\n            background: $pf-red;\n          }\n        }\n\n        & .sub-menu {\n          display: unset;\n        }\n\n        a {\n          padding: 1em 2em;\n          display: inline-block;\n          width: 100%;\n\n          &:hover,\n          &:focus {\n            background: $pf-red;\n          }\n        }\n      }\n\n      .menu-item-has-children {\n        display: flex;\n        flex-wrap: wrap;\n        align-items: center;\n\n        & > a {\n          flex: 1 0 80%;\n        }\n\n        .sub-menu-btn {\n          height: 100%;\n          flex: 1 0 10%;\n          height: 56px;\n        }\n\n        .sub-menu {\n          overflow: hidden;\n          max-height: 0;\n          transition: max-height 1s;\n          visibility: hidden;\n          flex-basis: 100%;\n\n          a {\n            &:hover,\n            &:focus {\n              background: $pf-light-red;\n              outline-offset: -0.25rem;\n            }\n          }\n        }\n\n        &.open {\n          background: $pf-red;\n\n          .sub-menu {\n            height: fit-content;\n            max-height: 500px;\n            visibility: visible;\n\n            a {\n              padding-left: 4rem;\n            }\n          }\n        }\n      }\n    }\n\n    /*\n  * DESKTOP STYLING\n  */\n    @include media-breakpoint('lg') {\n      display: none;\n      height: 100%;\n      font-weight: bold;\n      text-transform: uppercase;\n\n      ul.main-navigation {\n        height: 100%;\n        display: flex;\n        align-items: center;\n        // gap: 1rem;\n\n        & > .menu-item {\n          height: 100%;\n          display: flex;\n          align-items: center;\n          padding: 0 1rem;\n\n          &:hover,\n          &:focus-within {\n            background-color: $pf-dark-red;\n            color: $pf-white;\n\n            .caret {\n              background-image: url('../../../images/icons/caret_white.svg');\n            }\n          }\n        }\n\n        & > li > a {\n          font-weight: bold;\n        }\n      }\n\n      @include media-breakpoint('lg') {\n        display: block;\n      }\n\n      // Has submenu\n      .menu-item-has-children {\n        position: relative;\n        width: fit-content;\n\n        .sub-menu {\n          display: block;\n          visibility: hidden;\n          background-color: $pf-dark-red;\n          position: absolute;\n          left: 50%;\n          margin-left: -150px; // Has to be half the sub-menu width to centre.\n          top: 100%;\n          max-height: 0;\n          width: 300px;\n\n          li {\n            &:hover,\n            &:focus {\n              background: $pf-red;\n              color: $pf-white;\n            }\n\n            a {\n              display: inline-block;\n              padding: 1em 2em;\n              width: 100%;\n            }\n          }\n        }\n\n        &.open,\n        &:hover {\n          color: $pf-off-white;\n          background: $pf-dark-red;\n\n          .caret {\n            pointer-events: none;\n            transform: rotate(180deg);\n            transition: rotate 300ms ease-out;\n            background-image: url('../../../images/icons/caret_white.svg');\n          }\n\n          .sub-menu {\n            transition: all 400ms ease;\n            visibility: visible;\n            max-height: 2000px;\n          }\n        }\n\n        &:hover {\n          .sub-menu-btn {\n            pointer-events: none;\n          }\n        }\n      }\n    }\n\n  /*\n  * CHEVRON BUTTON\n  */\n    .sub-menu-btn {\n      display: flex;\n      align-items: center;\n      height: 100%;\n\n      @include media-breakpoint('lg') {\n        margin-left: 0.25em;\n      }\n\n      &:disabled {\n        cursor: none;\n        pointer-events: none;\n      }\n\n      &[aria-expanded='true'] {\n        .caret {\n          transform: rotate(180deg);\n          transition: rotate 300ms ease-out;\n        }\n      }\n\n      &:hover,\n      &:focus {\n        cursor: pointer;\n        background-color: $pf-dark-red;\n      }\n\n    }\n\n    .caret {\n      background-image: url('../../../images/icons/caret_white.svg');\n      height: 25px;\n      width: 25px;\n      background-repeat: no-repeat;\n      background-position: center;\n      transform: rotate(0);\n      transition: rotate 300ms ease-out;\n      display: inline-block;\n\n      @include media-breakpoint('lg') {\n        background-image: url('../../../images/icons/caret_charcoal.sv